@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');

.friend_request h2{
    letter-spacing: 1px;
    font-family: 'Lora', serif;
}
.friend_card{
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
    border-radius: 0.3rem;
    padding: 12px 12px;
}
.friend_card p:nth-child(1){
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: 1px;
    margin-top: 12px;
}
.friend_card p:nth-child(2){
    margin-top: -20px;
    font-size: 1rem;
    color: rgb(194, 193, 193);
}

.no_friend_cont img{
    max-height: 40vh;
}

.no_friend_cont .head{
    font-size: 1.2rem;
    letter-spacing: 1px;
    font-weight: 500;
}
.no_friend_cont .desc{
    font-size: 0.9rem;
    color: rgb(129, 129, 129);
    margin-top: -10px;
    word-wrap: break-word;
    letter-spacing: 1px;
}


.no_friend_cont .btn-grad {background-image: linear-gradient(to right, #ff00cc 0%, #333399  51%, #ff00cc  100%)}
.no_friend_cont .btn-grad {
   margin: 10px;
   padding: 15px 45px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   width: 100%;
   background-size: 200% auto;
   color: white;            
   box-shadow: 0 0 20px #eee;
   border-radius: 10px;
   display: block;
   border: none;
   margin: 0 auto;
 }

.no_friend_cont .btn-grad:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }
 @media (max-width: 768px) { 
    .friend_info_cont p:nth-child(1){
        font-size: 0.9rem !important;
    }
    .friend_request h2{
        letter-spacing: 1px;
        font-family: 'Lora', serif;
        font-size: 1.5rem;
    }
  }
 @media (min-width: 992px) { 
    .no_friend_cont .btn-grad{
        width: 37%;
    }
 }

