.edit_profile input:nth-child(1){
    margin-top: 1.2rem;
}
.edit_profile input{
    width: 100%;
    margin-bottom: 1.2rem;
    border: none;
    border-bottom: 0.1rem solid rgb(230, 230, 230);
    padding-bottom: 1.3rem;
}
.edit_profile button{
        width: 100%;
        border: none;
        background-color: rgb(0, 195, 255);
        color: white;
        padding: 1rem 0;
        border-radius: 2rem;
        font-size: 1.2rem;
        text-transform: uppercase;
}
.edit_profile textarea{
    width: 100%;
    margin-bottom: 1.2rem;
    border: none;
    border-bottom: 0.1rem solid rgb(230, 230, 230);
    padding-bottom: 1.3rem;
}
.edit_profile textarea:focus{
    outline: none;
    height: auto;
}
.edit_profile select{
    width: 100%;
    padding: 1rem 0;
    border: none;
    border-bottom: 0.1rem solid rgb(230, 230, 230);
    margin-bottom: 1.2rem;
}
.edit_profile select:focus{
    outline: none;
}
.profile_pic{
    display: flex;
    align-items: center;
    justify-content: center;
}
.profile_pic .MuiAvatar-root{
    height: 6rem !important;
    width: 6rem !important;
}
