@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');
.avatar_img{
    width: 100%;
    text-align: center;
}

.profile .col-11 p{
    text-align:center;
    font-family: 'Playfair Display', serif;
}
.profile .col-11 p:nth-child(1){
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 1px;
    text-align: center;
}

.profile .col-11 p:nth-child(even){
    font-size: 1rem;
    color: gray;
    margin-top: -10px;
    letter-spacing: 2px;
    margin-bottom: 25px;
    text-align: center;
}
