.home{
    height: 82vh;
    background:#00f7ffbb
}
.home .name{
    font-size: 1.3rem;
    color: rgb(255, 255, 255);
    font-family: 'Original Surfer', cursive;
    margin-top: 8px;
    letter-spacing: 1.2px;
}
.logo{
    width: 8rem;
    background-color: rgb(238, 237, 237);
    border-radius: 50%;
    padding: 0.7rem;
}
.logo img{
    width: 7rem;
    border-radius: 50%;
}
.button_down{
    position: absolute;
    bottom: 0;
    width: 100%;
}
.button_down button{
    width: 100%;
    padding:1.3rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    color: white;
    border: none;
    letter-spacing: 2px;
}
.log_in_btn{
    background-color: rgb(19, 108, 143)
}
.sign_up_btn{
    background-color: rgb(250, 87, 65);
}
button:focus{
    outline: none;
}