.login_logo{
    width: 100%;
    background:#00f7ff
}
.login_logo p{
    font-size: 1.5rem;
    padding: 1rem 0rem;
    color: white;
    letter-spacing: 2px;
}
.log_head{
    text-transform: uppercase;
    font-size: 1.8rem;
    text-align: center;

}

.main_login_sec input, .register input{
    width: 100%;
    border: none;
    border-bottom: 0.1rem solid rgb(238, 236, 236);
    margin-bottom: 4rem;
    padding-bottom: 1.3rem;
    letter-spacing: 1.3px;
}
input:focus{
    outline: none;
}
.login_btn, .register_btn{
    width: 100%;
    border: none;
    background-color: #00f7ff;
    color: white;
    padding: 1rem 0;
    border-radius: 2rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 2px;
}
