.item{
    background-color:transparent;
    padding-top: 8px;
    padding-bottom: 6px;
    padding-left: 16px;
    padding-right: 16px;
    -webkit-align-items: center;
            align-items: center;
    transition: all 150ms linear;
}
.item:hover{
    background-color: rgba(0, 0, 0, 0.04);
}
.item p{
    font-size: 1rem;
    letter-spacing: 1px;
    color: black;
}
.item i{
    color: rgb(126, 126, 126);
    font-size: 1.3rem !important;
}
.ant-drawer-body{
    padding:0px !important;
}
.reason_input{
    width: 80%;
    border: none;
    border-bottom: 0.1rem solid rgb(192, 192, 192);
    padding: 10px 0px;
    font-size: 1rem;
    letter-spacing: 1px;
}
.feed{
    background-color: rgb(250, 250, 250);
    height: 100%;
}

.feed .fa-refresh{
    position: fixed;
    color: white;
    top: 82%;
    right: 5%;
    padding: 14px;
    border-radius: 50%;
    font-size: 1.1rem;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
    z-index: 2;
}

.feed .card{
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
    border-radius: 0.3rem;
}
.heading{
    height: 90px;
    position: relative;
}
.heading p {
    color: white;
    text-transform: uppercase;
    font-size: 1.3rem;
    letter-spacing: 1px;
    position: absolute;
    bottom: 0;
    padding-left: 1.5rem;
}

.card-body .title{
    font-size: 1.2rem;
    letter-spacing: 1.5px;
    text-transform: capitalize;
    font-family: 'Crimson Text', serif;
}
.card-body .description{
    color: rgb(131, 130, 130);
    letter-spacing: 1px;
}
.card-body .fa-heart{
    font-size: 1.4rem;
    color: rgb(197, 196, 196);
    margin-right: 1rem;
}
.filled{
    color: red !important;
}
.fa-trash{
    color: rgb(197, 196, 196);
    font-size: 1.4rem;
}
.sent_user{
    font-size: 0.9rem;
}
.fa-question-circle, .fa-info-circle{
    font-size: 1.4rem;
    color: rgb(197, 196, 196);
    margin-right: 1rem;

}
.no_msg_cont{
    text-align: center;
    margin: 0 auto;

    width: 100%;
    height: auto;
    padding: 20px 0px;
}
.no_msg_cont img{
    height: 40vh;
}
.no_msg_cont .head{
    font-size: 1.2rem;
    letter-spacing: 1px;
    font-weight: 500;
}
.no_msg_cont .desc{
    font-size: 0.9rem;
    color: rgb(129, 129, 129);
    margin-top: -10px;
    word-wrap: break-word;
    letter-spacing: 1px;
}

.no_msg_cont .btn-grad {
   margin: 10px;
   padding: 15px 30px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   border: none;
   color: white;            
   box-shadow: 0 0 20px #eee;
   border-radius: 10px;
   display: block;
   width: 70%;
 }

.no_msg_cont .btn-grad:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }

 @media (min-width: 992px) { 
    .no_msg_cont .btn-grad{
        width: 37%;
    }
 }


