@import url('https://fonts.googleapis.com/css2?family=Original+Surfer&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');

.search input{
    width: 100%;
    background-color: rgb(243, 243, 243);
    border: none;
    border-radius: 1rem;
    padding: 0.5rem 2rem;
    color: rgb(119, 119, 119);
}

.search_user_info p:nth-child(1){
    font-weight: 700;
    letter-spacing: 1px;
    font-family: 'Lora', serif;
}
.search_user_info p:nth-child(2){
    margin-top: -13px;
    color: rgb(173, 173, 173);
    letter-spacing: 1px;
    font-family: 'Lora', serif;
}
.no_user_container{
    text-align: center;
    margin-top: 20%;
}
.no_user_container img{
    height: 200px;
    width: 200px;
}
.no_user_container p{
    letter-spacing: 1px;
    font-family: 'Original Surfer', cursive;
}