.msg_form input{
    width: 100%;
    margin-bottom: 2rem;
    border: none;
    border-bottom: 0.1rem solid rgb(216, 216, 216);
    padding-bottom: 1.32rem;
    font-size: 1.1rem;
    letter-spacing: 1px;
}
.msg_form textarea{
    width: 100%;
    height: 120px;
    border: none;
    border-bottom: 0.1rem solid rgb(216, 216, 216);
    font-size: 1.1rem;
    letter-spacing: 1px;
}
.msg_form textarea:focus{
    outline: none;
}

.gif_modal .ant-modal-body{
    padding: 0px !important;
}
