}
.guess_img p{
    font-size: 1.2rem;
    margin-top: 10px;
    letter-spacing: 1px;
    font-family: 'Original Surfer', cursive;
}
.auto{
    width: auto !important;
}

.guess_input{
    width: 100%;
    border: none;
    border-bottom: 0.1rem solid rgb(182, 182, 182);
    font-size: 1.1rem;
    letter-spacing: 1px;
    margin-top: -20px;
    padding-bottom: 1rem;
    margin-bottom: 18px;
}
