.item{
    background-color:transparent;
    padding-top: 8px;
    padding-bottom: 6px;
    padding-left: 16px;
    padding-right: 16px;
    align-items: center;
    transition: all 150ms linear;
}
.item:hover{
    background-color: rgba(0, 0, 0, 0.04);
}
.item p{
    font-size: 1rem;
    letter-spacing: 1px;
    color: black;
}
.item i{
    color: rgb(126, 126, 126);
    font-size: 1.3rem !important;
}
.ant-drawer-body{
    padding:0px !important;
}
.reason_input{
    width: 80%;
    border: none;
    border-bottom: 0.1rem solid rgb(192, 192, 192);
    padding: 10px 0px;
    font-size: 1rem;
    letter-spacing: 1px;
}