.reg_comp input{
    width: 100%;
    margin-bottom: 1.2rem;
    border: none;
    border-bottom: 0.1rem solid rgb(230, 230, 230);
    padding-bottom: 1.3rem;
}
.reg_comp button{
        width: 100%;
        border: none;
        background-color: rgb(0, 195, 255);
        color: white;
        padding: 1rem 0;
        border-radius: 2rem;
        font-size: 1.2rem;
        text-transform: uppercase;
}
.reg_comp select{
    width: 100%;
    padding: 1rem 0;
    border: none;
    border-bottom: 0.1rem solid rgb(230, 230, 230);
    margin-bottom: 1.2rem;
}
.reg_comp select:focus{
    outline: none;
}