.user_profile_btn {background-image: linear-gradient(to right, #0575E6 0%, #021B79  51%, #0575E6  100%)}
.user_profile_btn {
    padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    width: 100%;
    background-size: 200% auto;
    color: white;            
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    display: block;
    border: none;
}

.user_profile_btn:hover {
    background-position: right center;
    color: #fff;
    text-decoration: none;
}
         
.profile_bg{
    height: 27vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    margin-bottom: 5rem;
    box-shadow: 0 3px 6px rgba(0 ,0 ,0 ,0.3)
}
.profile_bg i{
    font-size: 1.6rem;
    margin-right: 1rem;
    margin-top: 1rem;
    float: right;
    color: rgb(255, 244, 244);
}
.user_profile_pic{
    position: absolute;
    bottom: -50%;
    left: 50%;
    margin-left: -90px;
    background: rgb(238, 237, 237);
    width: 170px;
    border-radius:50% ;
    height: 170px;
    display: flex;
    align-items: center;
    justify-content: center
}
.user_profile_pic img{
    width: 150px;
    height: 150px;
    border-radius:50% ;
    object-fit: cover;
}

.user_full_name{
    font-size: 1.2rem;
    letter-spacing: 1px;
    text-transform: capitalize;
    font-weight: bold;
}
.user_name{
    font-size: 1rem;
    margin-top: -20px;
    color: rgb(161, 161, 161);
}
.friends_btn{
    border: none;
    width: 70%;
    padding: 10px 10px;
    background: transparent;
    background: rgb(1, 191, 238);
    color: white;
    font-size: 1rem;
    letter-spacing: 1px;
    border-radius: 1.2rem;
    margin-bottom: 1rem;
    margin-top: -10px;
}
.user_bio{
    font-size: 1rem;
    color: rgb(126, 126, 126);
    letter-spacing: 1px;
}
.mutual_head{
    font-size: 1rem;
    font-weight: bold;
    letter-spacing: 1px;
}
.mutual_sub_head{
    margin-top: -13px;
    color: rgb(146, 146, 146);
    letter-spacing: 1px;
}
.mutual_modal_friend{
    margin-top:-10px ;
    font-size: 0.9rem;
    font-weight: 500;
    color: rgb(0, 174, 255);
    letter-spacing: 1px;
    cursor: pointer
}
